.blog-single__title {
    font-size: 5rem;
    font-style: normal;
    line-height: 1.1;
    color: var(--col-pbh-dark);
    
    @media (max-width: 1200px) {
        font-size: 4rem;
    }
    
    @media (max-width: 768px) {
        font-size: 3rem;
        margin-bottom: 0.5rem;
    }
    
    @media (max-width: 480px) {
        font-size: 2rem;
    }
}
.blog-single__intro {
    max-width: 40ch;
}


/* Blog Index Styles */


.blog-index {
    background-color: var(--col-pbh-dark);
    h1 {
        font-size: 105px;
        font-family: var(--ff-title);
        font-weight: var(--fw-800);
        color: var(--col-pbh-white);
        text-transform: uppercase;
        letter-spacing: -.025em;
        margin-bottom: 1rem;
    }
}
.blog-card {
    display: grid;
    grid-template-columns: 50px auto;
    column-gap: 1rem;
    text-decoration: none;
    height: 100%;
    &__date {
        font-size: 2rem;
        font-family: var(--ff-title);
        color: var(--col-pbh-violet);
        margin-bottom: 0.5rem;
    }
    &__title {
        font-size: 1.2rem;
        color: var(--col-pbh-white);
        margin-bottom: 1rem;
    }
}

.insights-hero {
    position: relative;
    margin-top: var(--h-top);
    min-height: calc( 100vh - var(--h-top) );
    display: flex;
    overflow: hidden;
    background: var(--col-pbh-violet);
    
    // Mobile: Stack vertically with shorter height
    @media (max-width: 768px) {
        min-height: 200px;
        flex-direction: column;
    }
    
    // Content section (left side)
    &__content-section {
        position: relative;
        flex: 1;
        display: flex;
        align-items: center;
        z-index: 4;
        min-height: calc( 100vh - var(--h-top) );
        
        @media (max-width: 768px) {
            min-height: 200px;
            flex: none;
        }
        
        .container {
            max-width: 1360px;
            width: 100%;
            margin: 0 auto;
            padding: 0 15px;
        }
    }
    
    // Background image section (centered in right half)
    &__image-section {
        position: absolute;
        top: 0;
        right: 0;
        width: 50%;
        height: 100%;
        z-index: 2;
        
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            display: block;
        }
    }
    
    // Lightning overlay
    &__lightning-overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 3;
        pointer-events: none;
        
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }
    
    &__content {
        max-width: 460px;
        color: var(--col-pbh-dark);
        padding: 2rem 0;
        
        @media (max-width: 768px) {
            max-width: 100%;
            padding: 1rem 0;
        }
    }
    
    &__title {
        font-family: var(--ff-title);
        text-transform: uppercase;
        font-size: 8rem;
        font-weight: bold;
        margin-bottom: 1.5rem;
        line-height: 1.1;
        color: var(--col-pbh-dark);
        
        @media (max-width: 1200px) {
            font-size: 6rem;
        }
        
        @media (max-width: 768px) {
            font-size: 4rem;
            margin-bottom: 0.5rem;
        }
        
        @media (max-width: 480px) {
            font-size: 2.8rem;
        }
    }
    
    &__text {
        font-size: 1.1rem;
        line-height: 1.6;
        color: var(--col-pbh-dark);
        
        p {
            margin-bottom: 1rem;
            
            &:last-child {
                margin-bottom: 0;
            }
        }
        
        @media (max-width: 768px) {
            font-size: 0.9rem;
            line-height: 1.4;
        }
    }
}

// Mobile hero content section (below the hero on mobile)
.insights-hero-content {
    @media (max-width: 768px) {
        padding: 2rem 0;
        background: var(--col-pbh-violet);
        
        .container {
            max-width: 1360px;
            margin: 0 auto;
            padding: 0 15px;
        }
        
        .hero-content {
            color: var(--col-pbh-dark);
            
            h2 {
                font-size: 1.5rem;
                margin-bottom: 1rem;
            }
            
            p {
                font-size: 1rem;
                line-height: 1.5;
            }
        }
    }
    
    @media (min-width: 769px) {
        display: none;
    }
}
