@import "typography";
@import "colours";
@import "buttons";
@import "header";
@import "footer";
@import "blocks/blocks";
@import "work";
@import "blog";
@import "service";

body {
  background-color: var(--col-pbh-black);
  color: var(--col-pbh-white);
  // margin-top: var(--h-top);
}

@media (min-width: 992px) {
  .cols-lg-2 {
    columns: 2;
  }
  .cols-lg-3 {
    columns: 3;
  }
}

.sticky {
  position: sticky;
  top: 0; // var(--h-top);
  @media (min-width: 992px) {
    top: 0; //var(--h-top-desktop);
  }
  background-color: #fff;
  z-index: 1000;
}

#breadcrumbs {
  font-size: var(--fs-200);
  a {
    text-decoration: none;
    font-weight: var(--fw-800);
    color: var(--col-dark);
  }
}

.wp-block-group {
  @extend .container;
  padding: 3rem;
  & .container {
    padding-inline: 0;
  }
}
.container .container {
  padding-inline: 0;
}

.post_meta {
  margin-bottom: 1rem;
  display: flex;
  gap: 0.5rem;
  &__author {
    font-weight: var(--fw-800);
    text-decoration: none;
    color: var(--col-dark);
    &:hover {
      color: var(--col-red-400);
    }
  }
  &__date {
    color: var(--col-gold-400);
  }
}

.page {
  &__hero {
    width: 100%;
    aspect-ratio: 16 / 9;
    object-fit: cover;
    object-position: center;
  }
}

