.form-control {
  border-radius: 0;
}

.wp-block-group__inner-container {
  width: 100%;
  padding-right: var(--bs-gutter-x, 1.5rem);
  padding-left: var(--bs-gutter-x, 1.5rem);
  margin-right: auto;
  margin-left: auto;

  @media (min-width: 576px) {
    max-width: 540px;
  }
  @media (min-width: 768px) {
    max-width: 720px;
  }
  @media (min-width: 992px) {
    max-width: 960px;
  }
  @media (min-width: 1200px) {
    max-width: 1140px;
  }
  @media (min-width: 1400px) {
    max-width: 1320px;
  }
}
.alignwide {
  width: 100%;
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-left: var(--bs-gutter-x, 1.5rem);
  padding-right: var(--bs-gutter-x, 1.5rem);
}

.alignfull {
  width: 100vw;
  max-width: 100%;
  margin-left: 0;
  margin-right: 0;
}

.single-work {
  background-color: var(--col-pbh-white);
  color: var(--col-pbh-black);
}
.page {
  background-color: var(--col-pbh-black);
  color: var(--col-pbh-white);

  &__hero {
    width: 100%;
    aspect-ratio: 16 / 9;
    object-fit: cover;
    object-position: center;
  }

  article {
    line-height: 1.6666667;
    ul {
      margin-bottom: 1.3333333em;
      margin-top: 1.3333333em;
      padding-left: 1.5833333em;
    }
    padding-bottom: 2rem;
  }

  &__logo {
    max-width: 200px;
    max-height: 80px;
    object-fit: contain;
    object-position: left center;
  }

  .glide__slide {
    transition: opacity 0.3s ease;
    opacity: 0.4;
    pointer-events: none;
  }

  .glide__slide img {
    width: 100%;
    height: auto;
    display: block;
    aspect-ratio: 16 / 9;
    object-fit: cover;
  }

  .glide__slide.glide__slide--active {
    opacity: 1;
    pointer-events: auto;
  }

  .glide__arrows {
    display: flex;
    justify-content: space-between;
    padding-top: 1rem;
  }
  .glide__arrow {
    appearance: none;
    background-color: transparent;
    background-image: none;
    border: none;
  }

  .video-lightbox-wrapper {
    margin-inline: auto;
    position: relative;
    width: 100%;
    aspect-ratio: 16 / 9;
    overflow: hidden;
  }
}

h1 {
  font-stretch: condensed;
  font-style: italic;
  font-weight: 800;
  font-size: 1.875rem;
  line-height: 1;
  margin-bottom: 1em;

  @media (min-width: 768px) {
    font-size: 2.25rem;
    line-height: 2.5;
  }
  @media (min-width: 1024px) {
    font-size: 3rem;
    line-height: 1;
  }
}

.rotate-180 {
  transform: rotate(180deg);
}

.back a {
  -webkit-text-stroke-width: 0.1875rem;
  -webkit-text-stroke-color: #000;
  -webkit-text-fill-color: transparent;
  display: inline-block;
  text-decoration: none;
  font-weight: 800;
  font-stretch: condensed;
  font-style: italic;
  font-size: 2.25rem;
  line-height: 2.5rem;
  @media (min-width: 768px) {
    font-size: 3.75rem;
    line-height: 1;
  }
  @media (min-width: 1024px) {
    font-size: 4.5rem;
    line-height: 1;
  }

  transition: transform 0.15s cubic-bezier(0.4, 0, 0.2, 1);
  &:hover {
    transform: scale(1.1);
  }
}

.pagination a {
  -webkit-text-stroke-color: #e6007e !important;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 2px;

  text-decoration: none;
  display: inline-block;

  line-height: 1;
  font-size: 2.25rem;

  transition: transform 0.15s cubic-bezier(0.4, 0, 0.2, 1);

  @media (min-width: 768px) {
    font-size: 3rem;
  }
  @media (min-width: 1024px) {
    font-size: 3.75rem;
  }

  &:hover {
    transform: scale(1.1);
  }
}

.p-htop {
  padding-top: var(--h-top);
}
